import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import { graphql, useStaticQuery } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import React, { ReactNode } from "react";
import { Header } from "./header";
import SEO from "./seo";

type LayoutProps = {
    children: ReactNode;
    title: string | null;
    description?: string;
}

const Layout: React.FunctionComponent<LayoutProps> = (props: LayoutProps) => {
    library.add(fas, fab);
    const data = useStaticQuery(graphql`
        query {
            imageSharp(fluid: {originalName: {eq: "background.jpg"}}) {
                gatsbyImageData(width: 900, height: 300)
            }
        }
    `)

    return <>
        <SEO title={ props.title } description={ props.description }/>
        <section className={ classNames("g-layout-boxed", "g-bgi-v8") }>
            <main className={ classNames("container") }>
                <div className={ classNames("row", "justify-content-between") }>
                    <section className={ classNames("col-lg-3", "order-lg-1", "g-mb-80-lg", "g-pt-100--lg") }>
                        <Header/>
                    </section>
                    <section className={ classNames("col-lg-9", "order-lg-5", "g-mb-80", "g-px-0") }>
                        <section className={ classNames("g-mb-80") }>
                            <GatsbyImage image={ getImage(data.imageSharp) }
                                         alt={ "background image" }/>
                        </section>
                        <section className={ classNames("col-12", 'g-px-15') }>
                            { props.children }
                        </section>
                    </section>
                </div>
            </main>
        </section>
    </>
}

export default Layout;
