import classNames from "classnames";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React from "react";
import {graphql, Link, useStaticQuery} from "gatsby";

const Links: React.FunctionComponent = () => {

    const data = useStaticQuery(graphql`
        query {
            site {
                siteMetadata {
                    links {
                        icon {
                            prefix
                            iconName
                        }
                        link
                        name,
                        external
                    }
                }
            }
        }
    `)

    return <>
        {
            data.site.siteMetadata.links.map((item: any, index: number) => {
                return <li key={index} className={classNames("nav-item", "g-my-5")}>
                    {
                        item.external ?
                            <a className={classNames("d-block", "u-link-v5", "g-color-gray-dark-v4", "rounded", "g-py-8", "g-color-purple--hover")}
                                href={item.link} target={"_blank"}>
                                <FontAwesomeIcon width={14.61} height={12.98} className={"g-mr-2"} icon={item.icon}/>
                                {item.name}
                            </a> :
                            <Link
                                className={classNames("d-block", "u-link-v5", "g-color-gray-dark-v4", "rounded", "g-py-8", "g-color-purple--hover")}
                                to={item.link}>
                                <FontAwesomeIcon width={14.61} height={12.98} className={"g-mr-2"} icon={item.icon}/>
                                {item.name}
                            </Link>
                }
                </li>
            })
        }

    </>
}

export default Links;
