import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import { Link } from "gatsby";
import React, { useState } from "react";
import { Subject } from "rxjs";
import { delay } from "rxjs/operators";
import Links from "./links";
import TagList from "./taglist";

enum openState {
    open,
    closing,
    close
}

export const Header: React.FunctionComponent = () => {
    const [ isOpen, setIsOpen ] = useState(openState.close);
    const subject: Subject<openState> = new Subject<openState>();
    subject.pipe(delay(900)).subscribe((data: openState) => {
        if ( data === openState.closing ) {
            setIsOpen(openState.close);
        }
    });

    const toggle = (): any => {
        subject.next(isOpen === openState.close ? openState.open : openState.closing)
        setIsOpen(isOpen === openState.close ? openState.open : openState.closing);
    };

    return <div className={ classNames("u-header",
                                       "u-header--side",
                                       "c-navbar-side-left",
                                       "animate__animated",
                                       "animate__fast",
                                       { ["animate__fadeInDown"]: isOpen === openState.open },
                                       { ["animate__fadeOutUp"]: isOpen === openState.closing }) }>
        <div className={ classNames("u-header__section-container", "g-bg-white") }>
            <div className={ classNames("u-header__section", "u-header__section--light") }>
                <nav className={ classNames("navbar", "navbar-expand-lg", "container") }>
                    <div className={classNames('w-100')}>
                        <div className={ classNames('justify-content-between', 'w-100', 'd-flex', 'd-lg-block') }>
                            <Link to="/" className={ classNames("navbar-brand", "g-mb-60--lg") }>
                                Bruno Jan's Blogger
                            </Link>
                            <button onClick={ toggle }
                                    className={ classNames("navbar-toggler",
                                                           "btn",
                                                           "btn-md",
                                                           "g-line-height-1",
                                                           "g-brd-none",
                                                           "g-pa-0",
                                                           "collapsed") }
                                    type="button">
                                <FontAwesomeIcon className={ "g-mr-2" } size={ "lg" }
                                                 icon={ isOpen === openState.open ? faTimes : faBars }/>
                            </button>
                        </div>
                        <div className={ classNames("collapse",
                                                    "navbar-collapse",
                                                    "align-items-center",
                                                    "flex-sm-row",
                                                    "w-100",
                                                    "g-mt-20",
                                                    "g-mt-0--lg",
                                                    "g-mb-40",
                                                    { ["show"]: isOpen === openState.open || isOpen === openState.closing }
                        ) }>
                            <ul className={ classNames("navbar-nav",
                                                       "ml-auto",
                                                       "text-uppercase",
                                                       "g-font-weight-600",
                                                       "u-sub-menu-v1") }>
                                <Links/>
                                <li className={ classNames("nav-item") }>
                                    <hr className={ classNames("g-brd-gray-light-v4") }/>
                                </li>
                                <TagList/>
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    </div>
}
