import classNames from "classnames";
import React from "react";
import {graphql, useStaticQuery} from "gatsby";
import Tags from "./tags";

const TagList: React.FunctionComponent<any> = (props: any) => {
    const data = useStaticQuery(graphql`
        query {
            allMdx (filter: {
                internal: {type: {eq: "Mdx"}}, 
                frontmatter: {published: {eq: true}}
            }) {
                distinct(field: frontmatter___tags)
            }
        }
    `)

    return <li className={classNames('nav-item', 'g-my-5')}>
        <h3 className={classNames('h5', 'g-color-black', 'g-font-weight-600', 'mb-4')}></h3>
        <Tags tags={data.allMdx.distinct} />
    </li>
}

export default TagList;
