import { faTag } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";
import { Link } from "gatsby";
import React from "react";

const Tags: React.FunctionComponent<any> = React.memo<any>((props: any) => {
    return <ul className={ classNames('u-list-inline', 'mb-0', 'text-uppercase', props.className) }>
        {
            props.tags.sort((x: string) => x).map((item: string) => {
                return <li className={ classNames('list-inline-item', 'g-mb-10') } key={ item }>
                    <Link className={ classNames('u-tags-v1',
                                                 'g-color-gray-dark-v4',
                                                 'g-color-white--hover',
                                                 'g-bg-gray-light-v5',
                                                 'g-bg-purple--hover',
                                                 'g-font-size-12',
                                                 'g-rounded-50',
                                                 'g-py-4',
                                                 'g-px-15') }
                          to={ `/tags/${ item }` }>
                        <FontAwesomeIcon width={ 8 } className={ 'g-mr-2' } icon={ faTag }/>
                        { item }
                    </Link>
                </li>
            })
        }
    </ul>
});

export default Tags;
